class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            coronaJson: 'sdafas',
        };
    }

    render() {
        return (
            <div id={'app'}>
                <MapCorona/>
                <div className={'info'}>
                    <h1>Данные о корона вирусе</h1>
                    {newCoronaVirus.map(item => {
                        return <div className={'info-data'}>
                            <h3 className="info-data-header">{item["dateNumber"]}</h3>
                            <div className="info-data-info">
                                {item["dateTimeEvent"].map(i => {
                                    return <div><strong>{i["time"]}</strong>{i["text"].substr(5)}</div>
                                })}
                            </div>
                        </div>
                    })}
                </div>
            </div>
        );
    }
}

export default App;
