import ReactDOM from 'react-dom';
import static_data from './variables';
import './App.css';
import React from 'react';
import {StaticMap} from 'react-map-gl';
import DeckGL, {ScatterplotLayer} from 'deck.gl';
import {ArcLayer} from '@deck.gl/layers';
import {TextLayer} from '@deck.gl/layers';
import axios from 'axios';
import {Pie} from 'react-chartjs-2';
import {layer} from 'deck.gl';
import variables from './variables'
import {HexagonLayer} from '@deck.gl/aggregation-layers';

console.log(variables.newCoronaVirus)

// Set your mapbox access token here
const MAPBOX_ACCESS_TOKEN =
    'pk.eyJ1IjoiYWtvemxvdiIsImEiOiJjazZiMDJheGowZDNiM250aXcyZnZ6YXJyIn0.J5Hj_OvUO9aaD1fk6rzUCg';

// Initial viewport settings
const initialViewState = {
    latitude: 26,
    longitude: 29,
    zoom: 2,
    pitch: 0, //30
    bearing: 0,
};

class MapCorona extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            coronaJson: null,
            totalDeaths: null,
            totalConfirmed: null,
            totalCured: null,
        };
    }

    componentDidMount() {
        axios
            .get(static_data.link_corona_virus)
            .then(res => {
                let coronaData = [];
                for (let item of res.data.features) {
                    coronaData.push(item.attributes);
                }
                this.setState({
                    coronaJson: coronaData,
                });
            })
            .then(res => {
            });
    }

    render() {
        let data = this.state.coronaJson;
        console.log(data);
        const line = [
            new ArcLayer({
                id: 'great-circle-layer',
                data,
                pickable: false,
                widthMinPixels: 1,
                getStrokeWidth: 12,
                getSourcePosition: [114.3055, 30.5928],
                getTargetPosition: d => [d.Long_, d.Lat],
                getSourceColor: [255, 255, 255],
                getTargetColor: [255, 255, 255],
                opacity: .02,
            }),
        ];
        const scatter = new ScatterplotLayer({
            id: 'scatterplot-layer',
            data,
            pickable: true,
            stroked: true,
            filled: true,
            radiusScale: 30000,
            radiusMinPixels: 15,
            radiusMaxPixels: 50,
            opacity: 1,
            lineWidthMinPixels: 1,
            getPosition: d => [d.Long_, d.Lat],
            getRadius: d => Math.sqrt(d.Confirmed),
            getFillColor: d => [255, 140, 0],
            getLineColor: d => [213, 89, 0],
            onClick: info => setTooltip(info.object, info.x, info.y, this.state),
            // onClick: info => setTooltip(info.object, info.x, info.y, this.state),
        });
        const text = new TextLayer({
            id: 'text-layer',
            data,
            pickable: false,
            getPosition: d => [d.Long_, d.Lat],
            getText: d => String(d.Confirmed),
            getSize: 22,
            getAngle: 0,
            getTextAnchor: 'middle',
            getAlignmentBaseline: 'center',
            getColor: [255, 255, 255]
        });
        console.log(text)
        return (
            <div className={'map-corona'}>
                <div id="tooltip"></div>
                <DeckGL
                    initialViewState={initialViewState}
                    controller={true}
                    layers={[scatter, text, line]}
                >
                    <StaticMap
                        mapStyle="mapbox://styles/mapbox/dark-v9"
                        mapboxApiAccessToken={MAPBOX_ACCESS_TOKEN}
                    ></StaticMap>
                </DeckGL>
            </div>
        );
    }
}

function setTooltip(object, x, y, state) {
    const el = document.getElementById('tooltip');
    if (object) {
        let chartData = {
            datasets: [
                {
                    data: [object.Confirmed, object.Deaths, object.Recovered],
                    backgroundColor: [
                        'rgba(255, 206, 86, 0.5)',
                        'rgba(255, 99, 132, 0.5)',
                        'rgba(11,123,59, 0.5)',
                    ],
                    borderColor: [
                        'rgba(255, 255, 255, 1)',
                        'rgba(255, 255, 255, 1)',
                        'rgba(255, 255, 255, 1)',
                    ],
                },
            ],
            labels: ['Confirmed', 'Deaths', 'Recovered'],
        };

        let chartOptions = {
            legend: {
                position: 'right',
                labels: {
                    boxWidth: 20,
                    fontColor: '#fff',
                },
            },
            title: {
                display: true,
                text: '',
                fontColor: '#fff',
                fontSize: 16,
                fontStyle: 'normal',
            },
            elements: {
                arc: {
                    // borderWidth: 18,
                },
            },
            cutoutPercentage: 30,
            responsive: true,
        };

        chartOptions.title.text = object.Country_Region;
        if (object.Confirmed) {
            chartData.labels[0] = `Confirmed - ${object.Confirmed}`;
        }
        if (object.Deaths) {
            chartData.labels[1] = `Deaths - ${object.Deaths}`;
        }
        if (object.Recovered) {
            chartData.labels[2] = `Cured - ${object.Recovered}`;
        }

        ReactDOM.render(<Pie data={chartData} options={chartOptions}/>, el);
        el.style.display = 'block';
        el.style.padding = '1rem 1rem 2rem 0'
        // el.style.left = x + 'px';
        // el.style.top = y + 'px';
    } else {
        el.style.display = 'none';
    }
}

export default MapCorona;
